import styled from "styled-components";

const location = window.location.pathname

const TopSectionContainer = styled.div`
  display: flex;
  justify-content: center;
  position: absolute;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0;
  top: 0;
  left: 0;
  background-color: #1756dd32;
  flex-direction: column;
  align-items: center;
  z-index: 99;
`

const Logo = styled.h1`
  margin: 0;
  margin-bottom: 1em;
  color: #fffd;
  font-weight: 700;
  font-size: 40px;
  text-align: center;
`

const Slogan = styled.h4`
  margin: 0;
  margin-bottom: 1.2em;
  color: #fffa;
  font-weight: 500;
  font-size: 24px;
  max-width: 500px;
  text-align: center;
`

const Paragraph = styled.p`
  display: block;
  margin: 0;
  margin-bottom: 2.4em;
  color: #fffd;
  font-size: 15px;
  line-height: 1.5;
  font-weight: 300;
  max-width: 700px;
  text-align: center;
`

const Enter = styled.button`
  outline: none;
  border: none;
  color: #ffff;
  background-color: #5c6ac48d;
  font-size: 16px;
  font-weight: 500;
  border-radius: 8px;
  padding: 10px 2em;
  cursor: pointer;
  border: 3px solid transparent;
  transition: all 350ms ease-in-out;

  a {
    color: #fffb;
    text-decoration: none;
    transition: all 350ms ease-in-out;
  }

  &:hover {
    background-color: #5c6ac4ff;
  }
`

export function TopSection() {
  return (
    <TopSectionContainer>
      <Logo>Thought Repository</Logo>
      <Slogan>(Carbon Neutrality)</Slogan>
      <Paragraph>
                           .     '0'    ' (Net Zero),  (Carbon Zero)' .
      </Paragraph>
      <Paragraph>
                    ,   ,          (Negative Emission Technique, NET)     .
      </Paragraph>
      <a href={location.includes("page")?'../world':'world'}><Enter> </Enter></a>
    </TopSectionContainer>
  )
}
